import React from "react"
import css from "./styles.module.scss"
import cx from "classnames"
import { Button } from "antd"
import ScreenButton from "../../../components/ScreenButton"
import ArcadeButton from "../../../components/ArcadeButton"

const STATE_STARTING = "starting"
const STATE_SHOWING_ANSWERS = "showingAnswers"
const STATE_ANSWERING = "answering"
const STATE_LOST = "lost"
const STATE_WON = "won"

const speed = 1000

const SimonSays = ({ onDone, rounds = 6 }) => {
  const [state, setState] = React.useState(STATE_STARTING)
  const answer = React.useRef([])
  const interval = React.useRef()
  const currentAnswerIndex = React.useRef(-1)
  const currentGuessIndex = React.useRef(0)
  const [active, setActive] = React.useState()

  const showNextAnswer = () => {
    currentAnswerIndex.current += 1
    if (currentAnswerIndex.current >= answer.current.length) {
      clearInterval(interval.current)
      setState(STATE_ANSWERING)
      setActive()
    } else {
      setActive(answer.current[currentAnswerIndex.current])
      setTimeout(() => setActive(), speed * 0.7)
    }
  }

  const handleClick = (e) => {
    if (state !== STATE_ANSWERING) return
    const { value } = e.target.dataset
    currentGuessIndex.current += 1
    if (answer.current[currentGuessIndex.current] === value) {
      if (currentGuessIndex.current >= answer.current.length - 1) {
        if (currentGuessIndex.current + 1 === rounds) {
          //   setState(STATE_WON)
          onDone()
        } else {
          handleStartNewRound()
        }
      }
    } else {
      setState(STATE_LOST)
    }
  }

  const handleStart = () => {
    answer.current = []
    handleStartNewRound()
  }

  const handleStartNewRound = () => {
    setState(STATE_SHOWING_ANSWERS)
    currentAnswerIndex.current = -1
    currentGuessIndex.current = -1
    answer.current.push(`${Math.floor(Math.random() * 4)}`)
    showAnswers()
  }

  const showAnswers = () => {
    interval.current = setInterval(showNextAnswer, speed)
  }
  React.useEffect(() => () => clearInterval(interval.current), [])

  return (
    <div className={cx(css.simon, { [css.locked]: state === STATE_SHOWING_ANSWERS })}>
      <div className={css.screen} hidden={state === STATE_SHOWING_ANSWERS || state === STATE_ANSWERING}>
        <ScreenButton hidden={state !== STATE_STARTING} onClick={handleStart}>
          START
        </ScreenButton>
        <ScreenButton hidden={state !== STATE_LOST} onClick={handleStart}>
          TRY AGAIN
        </ScreenButton>
        <ScreenButton hidden={state !== STATE_WON} onClick={onDone}>
          YOU WON, Continue
        </ScreenButton>
      </div>
      <div hidden={state !== STATE_SHOWING_ANSWERS && state !== STATE_ANSWERING} className={css.container}>
        <ArcadeButton isPushed={active === "0"} data-value={0} glow={true} variant="yellow" onClick={handleClick}></ArcadeButton>
        <ArcadeButton isPushed={active === "1"} data-value={1} glow={true} onClick={handleClick}></ArcadeButton>
        <ArcadeButton isPushed={active === "2"} data-value={2} glow={true} variant="blue" onClick={handleClick}></ArcadeButton>
        <ArcadeButton isPushed={active === "3"} data-value={3} glow={true} variant="green" onClick={handleClick}></ArcadeButton>
      </div>
    </div>
  )
}

export default SimonSays

